.card {
    position: relative;
}

.card img {
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}

.card .front {
    position: absolute;
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
}

.flipped  .front{
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

.card .cover {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}

.flipped .cover {
    transform: rotateY(90deg);
    transition-delay: 0;
}